const BASE_URL = 'http://shop-roles.asmer.fs.a-level.com.ua/graphql';
const gql = (url, query, variables) => {
    return fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ query, variables })
    })
        .then(res => res.json());
}

const App = () => {
  const onGet = () => {
      const query = 'query cats($q:String){CategoryFind(query:$q){_id,name,goods{_id,name}}}';
      gql(BASE_URL, query, { q: '[{}]' })
          .then(({ data }) => {
              console.log('[THEN]', data)
          })
          .catch((err) => {
              console.log('[CATCH]', err)
          })
  };

  return (
      <>
        <h1>Hello, New Theme!</h1>
        <button type='button' onClick={onGet}>GET</button>
      </>
  );
}

export default App;
